import React from 'react'

import PropTypes from 'prop-types'

export default function Lyrics(props) {

    // Hardcoded lyrics for proof of concept
    const defaultTitle = "Caos"
    const defaultLyrics = (<div>
        <p>De pronto todo cambia y ellos llaman a decirme <br />
            Seor debe hacer, lo que sola hacer<br />
            Y no se parece en nada a lo que usted viene<br />
            Haciendo desde tiempo atrs, algn tiempo atrs<br />
        </p>
        <p>Y hoy le llame a contarle que tengo respuestas<br />
            A preguntas que no entiendo bien<br />
            Hoy abri el agua caliente y luego la fra<br />
            Y no sabe usted lo que encontr</p>
        <p>De pronto desespero pues dicen que el comunismo<br />
            No esta nada bien, en la pelicula de ayer<br />
            Si esto es Cuba o Venezuela<br />
            o la Rusia setentera que vamos a hacer<br />
            Nos bombardearan tambin?</p>
        <p>Y hoy le llame a contarle que tengo<br />
            Respuestas a preguntas que no entiendo bien<br />
            Hoy abr el agua caliente y luego la fra<br />
            Y no sabe usted lo que encontr<br />
            Y hoy lei en Hotmail que Bill Gates me daria<br />
            Un centavo por cada email que enviara<br />
            Viajando en el bus oi opiniones y pues las robe<br />
            Que ms iba a hacer? Que ms iba a hacer?</p>
        <p>De pronto Dios, de quienes claman<br />
            Ser sus hijos se olvid y aqu estoy yo<br />
            Adoptado en un orfanato de ateos<br />
            Me dejo y al salir me confes</p>
        <p>Lo siento pero ya debo renunciar<br />
            En ustedes confiare<br />
            Para el mundo guiar</p>
        <p>Lo siento pero ya debe renunciar<br />
            En nosotros confi ya para el mundo guiar</p>
        <p>De pronto Dios, de quienes claman<br />
            Ser sus hijos se olvid y aqu estoy yo<br />
            Adoptado, en un orfanato de ateos me dejo<br />
            Y aqu estoy yo...</p>
    </div>)

    return (
        <div>
            <h1 className='mt-5 mb-3'>{props.title || defaultTitle}</h1>
            {props.children}
            {props.lyrics || defaultLyrics}
        </div>
    )
}


// Add proptypes with fields children, title and lyrics
Lyrics.propTypes = {
    children: PropTypes.node,
    title: PropTypes.string,
    lyrics: PropTypes.string
}